import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation } from 'swiper';

import { ProductCard, ProductLoading } from '../chuncks';

function Products({
	title = "",
	isSlider = false,
	isLoading = true,
	items,
	itemsLoading = 5,
}) {

	return (
		<section className="products-section">
			<div className="_container">
				<div className="products-section__body">
					{title &&
						<div className="products-section__top">
							<h2 className="products-section__title">{title}</h2>
						</div>}
					{isSlider ?
						<Swiper
							className="products-section__slider"
							modules={[Navigation]}
							slidesPerView={1}
							navigation
							spaceBetween={20}
							allowTouchMove={false}
							breakpoints={{
								991.98: {
									slidesPerView: 4,
								},
								767.98: {
									slidesPerView: 3,
								},
								479.98: {
									slidesPerView: 2
								}
							}}
						>
							{isLoading ?
								[...Array(itemsLoading)].map((item, idx) => (
									<SwiperSlide key={`product-loading_${idx}`}>
										<ProductLoading />
									</SwiperSlide>
								)) :
								items.map((item, idx) => (
									<SwiperSlide key={`${item.title}_${idx}`}>
										<ProductCard
											{...item}
										/>
									</SwiperSlide>
								))}
						</Swiper> :
						<div className="products-section__items">
							{isLoading ?
								[...Array(itemsLoading)].map((item, idx) => (
									<ProductLoading
										key={`product-loading_${idx}`}
									/>
								)) :
								items.map((item, idx) => (
									<ProductCard
										key={`${item.title}_${idx}`}
										{...item}
									/>
								))}
						</div>}
				</div>
			</div>
		</section >
	);
}

export default Products;